import { getLoggedUser } from "./getLoggedUser";

export default async function getTransacoes(consult) {
    const loggedUser = await getLoggedUser();

    let mockData = [
        {
            "codExterno": "90150",
            "nome": "Oramento Indstria"
        },
        {
            "codExterno": "90151",
            "nome": "Oramento Comrcio"
        },
        {
            "codExterno": "90155",
            "nome": "Oramento Consumo (IPI na Base de ICMS)"
        },
        {
            "codExterno": "90166",
            "nome": "Oramento IPI Suspenso e ICMS Diferido"
        }
    ]

    let headers = new Headers();
    headers.append("Content-Type", "application/json");

    let raw = JSON.stringify(
        {
            idCvtccnpj: loggedUser.cId,
            numreg: 750,
            pRotina: "filtrarTransacao",
            pCodCli: loggedUser.rId,
            pFilter: {
                filtro: {
                    filtroTns: consult
                }
            }
        }
    );

    let config = {
        method: 'POST',
        headers: headers,
        body: raw,
        redirect: 'follow'
    };

    // return mockData
    try {
        let request = await fetch("https://rex.clicvenda.com.br/cvIntegradorSenior/rest/Listas/executarRequisicaoExterna", config)
        let response = await request.json()
        return response.resultado
    } catch (err) {
        console.error({ message: 'Erro na requisio!', details: err })
    }
}